.navigation{
    display: flex;
    justify-content: flex-end;
    background-color: rgb(30, 41, 57);
    _position: fixed;
    width: 100%;
}

.navigation ul{
    list-style: none;
    display: flex;
}

.navlink, .active{
    color: white;
    list-style:  none;
    text-decoration: none;
}

.navigation ul li{
    padding: 20px;
}

.active{
    color: #fb6d3a;
}

.about-section{
    padding-top: 100px !important;
    display: flex;
    justify-content: space-between;
}

.my-photo{
    max-height: 460px;
    box-shadow: rgb(34, 36, 37) 0px 30px 18px 5px;
}

.about-text{
    margin-left: 30px;
    color: white;
    line-height:2.1em;
    text-align: justify;
}

.thick-text{
    font-weight: 700;
    font-size: 1.3em;
    color: #fb6d3a;
}
.main-section{
    background: radial-gradient(circle, rgba(195,173,173,1) 0%,  rgb(37, 50, 70) 77%);
    background-size: cover;
    background-position: center;
    padding-bottom: 60px;
}

.container{
    width: 900px;
    margin: 0 auto;
    padding-top: 50px;
}

.container h1{
    color: white;
    text-align: center;
    margin-bottom: 30px;
    text-transform: uppercase;
}

.items{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px;
    flex-basis: calc(100%/2 - 40px);
    background-color: rgba(80, 220, 255, 0.164);
    border-radius: 20px;
    box-shadow: rgb(15, 20, 27) 0px 30px 18px 5px;
    transition: background-color 0.8s 0.2s ease;
    cursor: pointer;
    height: 260px;
}

.item-react{
    padding: 20px;
}

.item-react h3{
    text-transform: uppercase;
    font-weight: 900;
    font-size: 1.6rem;
}

.item:hover{
    background-color: rgba(252, 138, 31, 0.164);
}

.js-img{
    width: 376px;
    padding: 4px;
}

.html-css-img{
    width: 280px;
    padding: 6px;
}

.react-img{
    width: 250px;
    margin: 0 auto;

}

.react_redux-img{
    width: 270px;
    margin: 20px auto;
}
.project-section{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-bottom: 50px;
}

.project-items{
    display: flex;
    flex-wrap: wrap;
}

.project-item{
    flex-basis: 100%;
    padding: 10px 10px 6px;
    margin: 16px;
    display: flex;
    background-color: rgba(0,0,0,0.2);
    border-radius: 6px;
}

.project-item a{
    text-decoration: none;
}

.design{
    width: 500px;
    align-self: flex-start;
    transition: all 0.8s ease-in;
}

.design:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
}

.description{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: white;
    padding-left: 16px;
    margin-left: 20px;
}

.description h3{
    text-transform: uppercase;
}

.description a{
    text-decoration: none;
    color: #fb6d3a;
}

.description li{
    padding: 5px 0;
}
.my_contacts{
    width: 100%;
    margin: 0 auto;
    border-radius: 20px;
}

.icon-box{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon{
    margin-top: 26px;
    color: white;
}

.icon-link{
    margin-left: 24px;
    color: white;
    text-decoration: none;
}

.first-link{
    margin-left: 6px;
}

.icon-link:hover,
.icon:hover{
    color: #fb6d3a;
}
.main-section-cv{
    background-image: url(/Portfolio.io/static/media/cv-background.e09c0269.jpg);
    background-position: center;
    background-size: cover;
    height: 100vh;
    overflow: hidden;
}

.cv-image{
    height: 700px;
    margin: 20px auto;
    display: block;
}

.photo-map{
    height: 400px;
    width: 600px;
    margin: 100px auto 0px;
}

.to-pdf {
    text-decoration: none;
    float:right;
    font-weight: 700;
    color: #fb6d3a;
    text-transform: uppercase;
}
.App {
  text-align: center;
}
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  font-family:'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
